import { isNullOrEmptyString } from './utils';

/**
 * @hidden
 */
type Pair = [string, string];

/**
 * @hidden
 */
const empty: Pair = ["", ""];

/**
 * @hidden
 */
const concat = (left: Pair, right: Pair): Pair => [left[0] + right[0], left[1] + right[1]];

/**
 * @hidden
 */
const notEmpty = (member: string): boolean => !isNullOrEmptyString(member);

/**
 * @hidden
 */
const parseMember = (member: string, idx: number, length: number): Pair => {
    let first = "(";

    const index = member.indexOf('[');

    if (index === -1) {
        member = "." + member;
    } else if (index > 0) {
        first += "(";
        member = `.${member.substring(0, index)} || {})${member.substring(index)}`;
    }

    member += (idx < length - 1) ? " || {})" : ")";

    return [first, member];
};

/**
 * @hidden
 */
const wrapExpression = (members: string[], paramName: string) => (members
        .filter(notEmpty)
        .reduce((pair, member, idx, arr) => concat(pair, parseMember(member, idx, arr.length)), empty)
        .join(paramName)
);

/**
 * @hidden
 */
export const expr = (expression: string = "", safe: boolean = false, paramName: string = "d"): string => {
    if (expression && expression.charAt(0) !== "[") {
        expression = "." + expression;
    }

    if (safe) {
        expression = expression.replace(/"([^.]*)\.([^"]*)"/g, '"$1_$DOT$_$2"');
        expression = expression.replace(/'([^.]*)\.([^']*)'/g, "'$1_$DOT$_$2'");
        expression = wrapExpression(expression.split("."), paramName);
        expression = expression.replace(/_\$DOT\$_/g, ".");
    } else {
        expression = paramName + expression;
    }

    return expression;
};

/**
 * @hidden
 */
const getterCache: {[key: string]: Function} = {};

/**
 * @hidden
 */
export const getter = (expression: string, safe?: boolean): Function => {
    const key = expression + safe;
    return getterCache[key] = getterCache[key] || new Function("d", "return " + expr(expression, safe));
};




